.videoRow{
    display: flex;
    align-items: center;
    margin-bottom: 30px;
    max-width: 700px;
}
.videoRow>img{
    object-fit: contain;
    height: 146px;
    width: 138px;
}
.videoRow_text{
    margin-left: 14px;
}
.videoRow_description{
    margin-top: 20px;
    font-size: 12px;
    color: #606060;
}
.videoRow_headline{
    font-size: 12px;
    color: #606060;
}
.videoRow_subs{
    padding: 2px;
    background-color: lightgray;
}
.videoRow_subsNum{
    font-weight: bolder;
    color: rgb(84,157,211);
}